(function () {
   'use strict';

   angular
      .module('sda.home')
      .directive('sdaBillboard', sdaBillboard);

   /** @ngInject */
   function sdaBillboard() {
      var directive = {
         restrict: 'E',
         templateUrl: 'app/home/components/sda-billboard/sda-billboard.html',
         transclude: true,
         replace: true,
         scope: {
            slides: '='
         },
         controller: BillboardController,
         bindToController: true,
         controllerAs: 'vm'
      };

      return directive;

      /** @ngInject */
      function BillboardController() {
         var vm = this;

         vm.setActive = setActive;
         vm.nextSlide = nextSlide;
         vm.prevSlide = prevSlide;

         activate();

         function activate() {
            if (vm.slides.length > 0) {
               setActive(vm.slides[0]);
            }
         }

         function setActive(slide) {
            vm.slides.forEach(function (s) {
               s.active = false;
            });

            slide.active = true;
         }

         function nextSlide() {
            var currentIndex = vm.slides.findIndex(function (slide) {
               return slide.active;
            });

            var nextIndex = (currentIndex + 1) % vm.slides.length;
            setActive(vm.slides[nextIndex]);
         }

         function prevSlide() {
            var currentIndex = vm.slides.findIndex(function (slide) {
               return slide.active;
            });

            var prevIndex = (currentIndex + vm.slides.length - 1) % vm.slides.length;
            setActive(vm.slides[prevIndex]);
         }
      }
   }

}());

(function () {
   'use strict';

   angular
      .module('sda.reader')
      .controller('ReaderSearchController', ReaderSearchController);

   /** @ngInject */
   function ReaderSearchController($stateParams, $scope, articleRepository, $http) {
      var vm = this;

      vm.queryResult = {};
      vm.displayFirstPage = displayFirstPage;
      vm.displayPrevPage = displayPrevPage;
      vm.displayNextPage = displayNextPage;
      vm.displayLastPage = displayLastPage;

      activate();

      function activate() {
         vm.query = $stateParams.query;

         if (vm.query) {
            $scope.$emit('set:query', vm.query);
            vm.queryResult = articleRepository.search({ q: vm.query });
         }
      }

      function displayLastPage() {
         if (vm.queryResult.last) {
            $http.get(vm.queryResult.query.last.uri)
               .then(function (resp) {
                  vm.queryResult = resp.data;
               });
         }
      }

      function displayNextPage() {
         if (vm.queryResult.next) {
            $http.get(vm.queryResult.query.next.uri)
               .then(function (resp) {
                  vm.queryResult = resp.data;
               });
         }
      }

      function displayPrevPage() {
         if (vm.queryResult.previous) {
            $http.get(vm.queryResult.query.previous.uri)
               .then(function (resp) {
                  vm.queryResult = resp.data;
               });
         }
      }

      function displayFirstPage() {
         if (vm.queryResult.first) {
            $http.get(vm.queryResult.query.first.uri)
               .then(function (resp) {
                  vm.queryResult = resp.data;
               });
         }
      }
   }

})();

(function () {
   'use strict';

   angular
      .module('sda.reader')
      .controller('ReaderPreviewController', ReaderPreviewController);

   var TYPE_TITLE = {
      detailed: 'Detailed Article'
   };

   /** @ngInject */
   function ReaderPreviewController($stateParams, articleCollectionRepository, articleRepository, _, cslBuilder) {
      var vm = this;

      vm.node = null;
      vm.article = null;
      vm.links = [];
      vm.citations = [];
      vm.bibliography = [];
      vm.getThemeTitle = getThemeTitle;

      activate();

      function activate() {
         // TODO: fall back to larger articles until one is found
         articleCollectionRepository.get({ id: $stateParams.id, type: 'summary'}, onThematicNodeLoaded);
      }

      function getThemeTitle(type) {
         return TYPE_TITLE[type] || _.capitalize(type);
      }

      function onThematicNodeLoaded(node) {
         vm.node = node;
         vm.article = node.article;
         vm.links = _.values(node.links);

         var citations = vm.article.citations;
         var bibliography = _.indexBy(vm.article.bibliography, 'id');

         cslBuilder.renderBibliography(bibliography, citations, 'mla').then(function (bibView) {
            vm.bibliography = bibView.items;
            vm.citations = _.pluck(bibView.citations, 'html');
         });
      }
   }

})();

(function () {
   'use strict';

   angular
      .module('sda.reader')
      .controller('ArticleController', ArticleController);

   /** @ngInject */
   function ArticleController($state, $stateParams, articleRepository, articleCollectionRepository, $log, $window, $scope, $timeout, _, cslBuilder) {
      var vm = this;

      vm.activeTab = null;

      vm.node = null;
      vm.article = null;
      vm.links = [];
      vm.articleType = null;

      vm.toc = [];
      vm.citations = [];
      vm.bibliography = [];

      vm.titleCase = _.startCase;
      vm.scrollTo = scrollTo;
      vm.scrollToTop = scrollToTop;
      vm.activateNote = activateNote;
      vm.goBack = goBack;
      vm.goToLink = goToLink;

      activate();

      function activate() {
         if ($stateParams.type) {
            loadThematic($stateParams.id, $stateParams.type);
         } else {
            loadArticle($stateParams.id);
         }

         $scope.$on('click:footnote', function (evt, data) {
            $scope.$apply(function () {
               activateNote(data.note, data.$event);
            });
         });

         $scope.$on('click:citation', function (evt, data) {
            $scope.$apply(function () {
               activateCitation(data.citation, data.$event);
            });
         });
      }

      /**
       * Asynchronously loads an article into the controller view model.
       *
       * @param {string} articleId
       */
      function loadArticle(articleId) {
         articleRepository.get({ id: articleId }, onArticleLoaded);
      }

      /**
      * Asynchronously loads an article via a thematic node into the controller view model.
      *
      * @param {string} nodeId
      * @param {string} type
      */
      function loadThematic(nodeId, type) {
         vm.articleType = type;
         articleCollectionRepository.get({ id: nodeId, type: type }, onThematicNodeLoaded);
      }

      /**
       * Callback for when the thematic collection node has been loaded.
       *
       * @param {ThematicNode} node
       */
      function onThematicNodeLoaded(node) {
         vm.node = node;
         vm.links = _.values(node.links);
         onArticleLoaded(node.article);
      }

      /**
       * Callback for when the article has been loaded.
       *
       * @param {Article} article
       */
      function onArticleLoaded(article) {
         vm.article = article;

         vm.article.links.forEach(function (link) {
            link.icon = getIcon(link.type);
         });

         var citations = vm.article.citations;
         var bibliography = _.indexBy(vm.article.bibliography, 'id');

         cslBuilder.renderBibliography(bibliography, citations, 'mla').then(function (bibView) {
            vm.citations = bibView.citations;
            vm.bibliography = bibView.items;
         });

         initScroll();
      }

      /**
       * Set initial scroll position on the page.
       *
       * Should be called after the article has been loaded.
       */
      function initScroll() {
         if ($stateParams.scrollTo) {
            // give page time to render before scrolling to target
            $timeout(function () {
               if ($stateParams.scrollTo.match(/footnote/)) {
                  var note = _.findWhere(vm.article.footnotes, {backlinkId: $stateParams.scrollTo});
                  if (note) {
                     activateNote(note);
                  }
               } else if ($stateParams.scrollTo.match(/cite/)) {
                  var citation = _.findWhere(vm.citations, {backlinkId: $stateParams.scrollTo});
                  if (citation) {
                     activateCitation(citation);
                  }
               } else {
                  scrollTo($stateParams.scrollTo, false)
               }
            });
         }
      }

      function scrollTo(id, animated, $event) {
         if ($event) {
            $event.preventDefault();
            $event.stopPropagation();
         }

         var container = angular.element('#article-content');
         var target = angular.element('#' + id);
         try {
            if (animated) {
               container.duScrollToElementAnimated(target);
            } else {
               container.duScrollToElement(target);
            }
         } catch (e) {
            $log.warn('unable to scroll to element by id', id);
         }
      }

      function scrollToTop(animated, $event) {
         if ($event) {
            $event.preventDefault();
            $event.stopPropagation();
         }

         var container = angular.element('#article-content');
         if (animated) {
            container.duScrollTopAnimated(0);
         } else {
            container.duScrollTop(0);
         }
      }

      function goBack() {
         var routeOpts = {};

         if (vm.node) {
            routeOpts.id = vm.node.id;
         }

         $state.go('sda.reader.main.preview', routeOpts);
      }

      function goToLink(link, $event) {
         if ($event) {
            $event.preventDefault();
            $event.stopPropagation();
         }

         var state = getState(link.type);
         if (state) {
            $state.go(state, { id: link.id });
         } else {
            $window.location = link.uri;
         }
      }

      function activateNote(note, $event) {
         vm.article.footnotes.forEach(function (note) {
            note.active = false;
         });

         note.active = true;
         vm.activeTab = 'footnotes';

         scrollTo(note.backlinkId, true, $event)
      }

      function activateCitation(citation, $event) {
         vm.activeTab = 'bibliography';
         scrollTo(citation.id, true, $event);
      }

      /**
       * Get the appropriate (font-awesome) icon for a given link type
       *
       * @param {string} type
       * @return {string}
       */
      function getIcon(type) {
         switch (type) {
            case 'article':
               return 'file-text-o';
            case 'book':
               return 'book';
            case 'video':
               return 'film';
            case 'audio':
               return 'volume-up';
            default:
               return 'link';
         }
      }

      /**
       * Get the appropriate app state for a given link type
       *
       * @param {string} type
       * @return {string}
       */
      function getState(type) {
         switch (type) {
            case 'article':
               return 'sda.reader.article';
            // case 'book':
            //    return 'sda.library-book';
            case 'video':
               return 'sda.media-item';
            case 'audio':
               return 'sda.media-item';
            default:
               return null;
         }
      }
   }

})();

(function () {
   'use strict';

   angular
      .module('sda.media')
      .controller('MediaItemController', MediaItemController);

   /** @ngInject */
   function MediaItemController($stateParams, $sce, videoRepository, $log) {
      var vm = this;

      vm.item = {};
      vm.speaker = {};
      vm.getYoutubeVideoUrl = getVideoUrl;

      activate();

      function activate() {
         var videoP = videoRepository.getVideo($stateParams.id);
         var speakerP = videoP.then(function (video) {
            return videoRepository.getSpeaker(video.speaker.id);
         });

         videoP.then(function (video) {
            vm.item = video;
         }, function (err) {
            $log.debug('unable to load video', err);
         });

         speakerP.then(function (speaker) {
            vm.speaker = speaker;
         }, function (err) {
            $log.debug('unable to load speaker', err);
         });
      }

      function getVideoUrl(id) {
         return $sce.trustAsResourceUrl('https://youtube.com/embed/' + id);
      }
   }

})();

(function () {
   'use strict';

   angular
      .module('sda.library')
      .controller('LibraryPeopleSearchController', LibraryPeopleSearchController);

   /** @ngInject */
   function LibraryPeopleSearchController($stateParams, $state, $scope, $timeout, personRepository) {
      var vm = this;

      vm.people = [];
      vm.query = '';
      vm.currentId = null;
      vm.loading = true;
      vm.loadingTimeout = null;
      vm.showThrobber = false;
      vm.search = search;

      $scope.$on('$stateChangeSuccess', function (evt, state, params) {
         if (state.name === 'sda.library.main.search-people') {
            if (params.id) {
               vm.currentId = params.id
            }
         } else {
            vm.currentId = null;
         }
      });

      activate();

      function activate() {
         if ($state.is('sda.library.main.search-people')) {
            vm.query = $stateParams.query;
            vm.currentId = $stateParams.id;

            if (vm.query) {
               search();
            }
         }
      }

      function search() {
         $state.go('sda.library.main.search-people', { query: vm.query, id: vm.currentId });

         personRepository.get({ q: vm.query }, onResultsLoaded);

         showThrobber();
         vm.loading = true;
      }

      function onResultsLoaded(results) {
         hideThrobber();
         vm.loading = false;

         vm.people = results.items;

         if (!vm.currentId && vm.people.length > 0) {
            vm.currentId = vm.people[0].id;
            $state.go('sda.library.main.search-people', { query: vm.query, id: vm.currentId });
         }
      }

      function showThrobber() {
         hideThrobber();

         // prevent throbber "flash" on quick load
         vm.loadingTimeout = $timeout(function () {
            vm.showThrobber = true;
            vm.loadingTimeout = null;
         }, 250);
      }

      function hideThrobber() {
         if (vm.loadingTimeout) {
            $timeout.cancel(vm.loadingTimeout);
            vm.loadingTimeout = null;
         }

         vm.showThrobber = false;
      }
   }

})();

