(function () {
   'use strict';

   angular
      .module('sda.reader')
      .run(runBlock);

   /** @ngInject */
   function runBlock($log, $rootScope, duScrollActiveClass) {
      // enable nested scrollspy behavior
      $rootScope.$on('duScrollspy:becameActive', function($event, $element){
         $element.parents('li').addClass(duScrollActiveClass);
      });

      $rootScope.$on('duScrollspy:becameInactive', function($event, $element){
         $element.parents('li').removeClass(duScrollActiveClass);
      });
   }

})();

(function () {
   'use strict';

   angular
      .module('sda.reader')
      .controller('ReaderController', ReaderController);

   /** @ngInject */
   function ReaderController($state, $scope, articleCollectionRepository, _) {
      var vm = this;

      vm.showBanner = true;

      vm.search = search;
      vm.query = '';
      vm.collection = null;

      vm.nodeHasArticles = nodeHasArticles;

      activate();

      function activate() {
         $scope.$on('set:query', function (e, query) {
            vm.query = query;
         });

         vm.collection = articleCollectionRepository.get();
      }

      function nodeHasArticles(node) {
         return _.size(node.articles) > 0;
      }

      function search() {
         $state.go('sda.reader.search', { query: vm.query });
      }
   }

})();

